* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
*::before,
*::after {
  box-sizing: border-box;
}
.skin {
  position: relative;
  height: 100vh;
  background-color: #ffe600;
  overflow: hidden;
  .nose {
    position: relative;
    left: 50%;
    top: 145px;
    margin-left: -10px;
    width: 0px;
    height: 0px;
    border: 10px solid #000;
    border-color: #000 transparent transparent;
    border-bottom: none;
    z-index: 10;
    &:hover{
        transform-origin: center bottom;
        animation: wave .3s infinite linear;
    }
    .yuan {
      position: absolute;
      width: 20px;
      height: 6px;
      top: -16px;
      left: -10px;
      border-radius: 14px 14px 0 0;
      background-color: #000;
    }
  }
  .eye {
    border: 2px solid #000;
    width: 64px;
    height: 64px;
    position: absolute;
    left: 50%;
    top: 100px;
    margin-left: -32px;
    background-color: #2e2e2e;
    border-radius: 50%;
    &::before {
      content: "";
      display: block;
      background-color: #fff;
      width: 30px;
      height: 30px;
      border: 3px solid #000;
      border-radius: 50%;
      position: relative;
      left: 8px;
      top: 2px;
    }
  }
  .left {
    transform: translateX(-100px);
  }
  .right {
    transform: translateX(100px);
  }
  .mouth {
    width: 160px;
    height: 160px;
    position: absolute;
    left: 50%;
    top: 175px;
    margin-left: -80px;
    .up {
      position: relative;
      top: -10px;
      z-index: 1;
      .lip{
        border: 3px solid #000;
        height: 30px;
        width: 78px;
        border-top-color: transparent;
        border-right-color: transparent;
        background-color: #ffe600;
        &::before {
            content: "";
            display: block;
            width: 7px;
            height: 30px;
            background-color: #ffe600;
          }
      }
      .lip.leftl {
        border-radius: 0 0 0 50px;
        position: relative;
        transform: rotate(-15deg);
        &::before {
          position: absolute;
          right: -5px;
          bottom: 0;
        }
        &::after {
            content: "";
            display: block;
            width: 80px;
            height: 5px;
            position: absolute;
            right: 0;
            top: -5px;
            background-color: #ffe600;
          }
      }
      .lip.rightl {
        border-radius: 0 0 50px 0;
        position: absolute;
        right: 0;
        top: 0;
        transform: rotate(15deg);
        &::before {
          position: absolute;
          left: -5px;
          bottom: 0;
        }
      }
    }
    .down{
        height: 160px;
        position: absolute;
        top: 0;
        width: 100%;
        overflow: hidden;
        .yuan1{
            border: 1px solid black;
            width: 140px;
            height: 500px;
            position: absolute;
            bottom: 0;
            left: 50%;
            margin-left: -70px;
            border-radius: 65%;
            background-color: #9b000a;
            overflow: hidden;
            .yuan2{
                position: absolute;
                bottom: -170px;
                left: 50%;
                margin-left: -80px;
                width: 160px;
                height: 300px;
                background-color: #ff485f;
                border-radius: 100px;
            }
        }
    }
  }
  .facel{
      position: absolute;
      left: 50%;
      border: 2px solid black;
      width: 88px;
      height: 88px;
      top: 200px;
      margin-left: -200px;
      background-color: red;
      border-radius: 50%;
      img{
          position: absolute;
          top: 50%;
          left: 50%;
          transform-origin: 0 0;
          transform: rotateY(180deg);
      }
  }
  .facer{
      position: absolute;
      left: 50%;
      border: 2px solid black;
      width: 88px;
      height: 88px;
      top: 200px;
      margin-left: 112px;
      background-color: red;
      border-radius: 50%;
      img{
        position: absolute;
        top: 50%;
        left: 50%;
        transform-origin: 0 0;
    }
  }
}

@keyframes wave {
    0%{
        transform: rotate(0deg);
    } 
    33%{
        transform: rotate(6deg);
    }
    66%{
        transform: rotate(-6deg);
    }
    100%{
        transform: rotate(0deg);
    }
}
